.ripple-dot {
  position: relative;
  margin: 30px 20px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #2f54eb;
}

.ripple-dot::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  -webkit-box-shadow: inset 0 0 10px #2f54eb;
          box-shadow: inset 0 0 10px #2f54eb;
  -webkit-animation: ripple 2500ms ease-out 500ms infinite;
          animation: ripple 2500ms ease-out 500ms infinite;
}

@-webkit-keyframes ripple {
  0% {
    opacity: 0.1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    opacity: 0.6;
  }
  100% {
    opacity: 0;
    -webkit-transform: scale(2);
            transform: scale(2);
  }
}

@keyframes ripple {
  0% {
    opacity: 0.1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    opacity: 0.6;
  }
  100% {
    opacity: 0;
    -webkit-transform: scale(2);
            transform: scale(2);
  }
}



