.border-gradient {
  width: 150px;
  height: 31px;
  background-color: rgba(24, 144, 255, 0.2);
  background-image: -webkit-gradient(linear, left top, right top, from(#2f54eb), to(transparent));
  background-image: -webkit-linear-gradient(left, #2f54eb, transparent);
  background-image: -o-linear-gradient(left, #2f54eb, transparent);
  background-image: linear-gradient(to right, #2f54eb, transparent);
  background-size: 100% 1px;
  background-position: left bottom;
  background-repeat: no-repeat;
}



