.bg-gradient {
  width: 150px;
  height: 30px;
  background: -webkit-gradient(linear, left top, right top, from(#2f54eb), to(transparent));
  background: -webkit-linear-gradient(left, #2f54eb, transparent);
  background: -o-linear-gradient(left, #2f54eb, transparent);
  background: linear-gradient(to right, #2f54eb, transparent);
}



