.imgbox {
  margin-top: 20px;
  position: relative;
  height: 180px;
}

.img {
  position: absolute;
  cursor: pointer;
}

.img1 {
  top: -36px;
}

.img2 {
  opacity: 0;
  -webkit-transition: opacity ease-out 600ms;
  -o-transition: opacity ease-out 600ms;
  transition: opacity ease-out 600ms;
}

.img2:hover {
  opacity: 1;
}



